ar a que participe activamente sin hacer que dicha participación sea obligatoria. Es necesario dejar que la persona en situación de discapacidad, según el caso, sea independiente y dejar que ella haga las cosas por sí sola, además de ello se recomiendan los siguientes tips para atender esta realidad:")]),_c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_vm._m(19),_c('div',{staticClass:"col-lg-7",attrs:{"data-aos":"fade-left"}},[_c('AcordionA',{staticClass:"mb-2",attrs:{"tipo":"a","clase-tarjeta":"tarjeta tarjeta-azul"}},[_c('div',{staticClass:"row",attrs:{"titulo":"En el recorrido"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Cuando se esté desarrollando un recorrido guiado, es fundamental que el guía le hable directamente a la persona en situación de discapacidad y no a su acompañante, ya que es un error común el explicarle al acompañante algunos sucesos o darle información puntual para este la retransmita a la persona que presenta una discapacidad. Se debe comprender que no todas las personas que presentan alguna discapacidad son iguales, cada una de ella presenta capacidades y desarrolla destrezas diferentes. ")])]),_c('div',{staticClass:"row",attrs:{"titulo":"En la planeación del recorrido"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Aprender los nombres de memoria si es un grupo pequeño y el nombre del tour o paquete si es un grupo grande.")])]),_c('div',{staticClass:"row",attrs:{"titulo":"Preparar su indumentaria"}},[_c('p',{staticClass:"mb-0"},[_vm._v("En el momento de la planeación del recorrido guiado, el guía de turismo debe identificar la cantidad de ayuda que necesitará para el desarrollo del servicio contratado, en algún caso se puede requerir de ayuda adicional, este personal también debe estar en la capacidad para atender al turista con seguridad y velar también por la seguridad de él mismo. ")])]),_c('div',{staticClass:"row",attrs:{"titulo":"En la comunicación"}},[_c('p',{staticClass:"mb-0"},[_vm._v("El guía deberá identificar tamb