oda la población en general.")])]),_c('div',{staticClass:"col-lg-4 col-md-6 col-6",attrs:{"data-aos":"fade-left"}},[_c('figure',{staticClass:"desaparece"},[_c('img',{attrs:{"src":require("@/assets/curso/tema3/1.jpg"),"alt":"Texto que describa la imagen"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-4 col-md-6 col-6",attrs:{"data-aos":"fade-right"}},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/tema3/2.jpg"),"alt":"Texto que describa la imagen"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"cajon color-primario p-4 mb-4",staticStyle:{"background-color":"#FFD667"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Turismo accesible es el conjunto integral de actividades y recorridos dentro de entornos e itinerarios que se presentan en los viajes, distintos al entorno habitual, de una o varias personas en condiciones de seguridad, confortabilidad y autonomía (p. 26).")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row align-items-center mb-4"},[_c('div',{staticClass:"col-lg-1 col-md-2 col-3",attrs:{"data-aos":"fade-right"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/icon1.svg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-lg-11 col-md-10 col-9",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Un importante avance ligado a la evolución de los medios electrónicos de comunicación es el diseño y publicación en Internet de guías turísticas "),_c('em',[_vm._v("on-line")]),_vm._v(" específicas sobre turismo accesible, en algunos casos por regiones geográficas, en otros por países y en numerosas ocasiones hasta por ciudades. El turista con discapacidad puede acceder a ellas desde cualquier computadora en forma gratuita, consultar instalaciones accesibles, servicios, excursiones 