bre las ruedas traseras, esto se debe hacer escalón por escalón. El cuerpo y el peso del guía que lleve la silla de ruedas servirá de mecanismo de freno. Sin duda alguna, el realizar esta maniobra requiere de tener buen estado físico, si no se tiene la plena seguridad de poder hacer la maniobra se debe solicitar ayuda, de esta manera se minimizan los riesgos de perder el control de la silla y accidentar al turista en situación de discapacidad. Recuerde que independiente del recorrido que se desarrolle, se deben verificar las condiciones de accesibilidad, así mismo se tienen que ubicar las rampas, ascensores y demás elementos que puedan ayudar a facilitar el desplazamiento en la realización del recorrido. ")])])]),_c('div',{staticClass:"row align-items-center mb-4"},[_c('div',{staticClass:"col-lg-7",attrs:{"data-aos":"fade-right"}},[_c('p',{staticClass:"mb-4"},[_vm._v("En la maniobra para subir un escalón o andén, se deben subir inicialmente las ruedas delanteras, enseguida se suben las ruedas traseras impulsando la silla hacia adelante. Cuando esté subiendo o bajando escalones, las ruedas de la silla deben estar apoyadas de manera simultánea. ")]),_c('p',{staticClass:"mb-4"},[_vm._v("Hay que recordar que las sillas de ruedas tienen algunas partes que son móviles como por ejemplo los apoya pies o los apoyabrazos, por ningún motivo trate de levantar la silla desde estas partes. Hay que evitar realizar maniobras peligrosas y que expongan a la persona en situación de discapacidad, como por ejemplo levantar entre varias personas la silla de ruedas, esto se hace únicamente cuando sea necesario y se deberá tener el consentimiento del turista en situación de discapacidad.")])]),_c('div',{staticClass:"col-lg-5",attrs:{"data-aos":"fade-left"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/img6.jpg")}})])])]),_c('div',{staticClass:"row mb-4",attrs:{"data-aos":"fade"}},[_c('div',{staticClass:"col-auto"},[_c('div',{staticClass:"sub p-2"},[_c('h4',{staticClas